@import url(https://fonts.googleapis.com/css2?family=Vollkorn:ital,wght@0,400;0,500;0,600;0,700;1,400;1,500;1,600;1,700&display=swap);
body {
  margin: 0;
  font-family: 'Vollkorn', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Vollkorn', serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.banner-section{
    height: 55px;
    position: relative;
}
.banner{
    padding-left: 20px;
    text-align: left;
    text-align: initial;
    font-size: 25px;
    margin: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.passenger-title-blk{
    padding: 20px;
    text-align: left;
    padding-bottom: 0px;
    color: white;
    font-size: 18px;
}

.passenger-data{
    padding: 20px;
    margin: 10px 20px 20px;
    background-color: white;
    border-radius: 4px;
    box-shadow: 2px 4px 10px #a2806f;
}

.input-blk{
    display: -webkit-flex;
    display: flex;
    margin: 10px 0px;
    -webkit-align-items: center;
            align-items: center;
}

.input-label{
    width: 150px;
    text-align: left;
    font-weight: 500;
    font-size: 18px;
}

.passenger-input{
    width: 65%;
    height: 35px;
    outline: none;
    border: 1px solid #0000002e;
    border-radius: 4px;
    padding-left: 8px;
    text-transform: uppercase;
}

.passenger-input .react-date-picker__inputGroup{
    text-align: left;
}
.booking-section{
    background-color: #2f81ed;
    background-image: linear-gradient(to right, #2F80ED, #56CCF2);
    display: table;
    position: absolute;
    left: 0;
    height: calc(100% - 55px);
    width: 100%;
}
.booking-form{
    width: 50%;
    padding: 30px;
    background: white;
    box-shadow: 2px 4px 10px #a2806f;
    margin: 55px 25%;
}

.trip-blk{
    display: -webkit-inline-flex;
    display: inline-flex;
}

.trip-option{
    margin-right: 20px;
    text-align: center;
    width: 100px;
    border: 1px solid #E72744;
    border-radius: 25px;
    height: 30px;
    padding-top: 3px;
    cursor: pointer;
    color: #E72744;
}

.trip-type{
    cursor: pointer;
}

.selected-trip{
    background: #E72744;
    color: white;
    border: none;
}

.form-field{
    width: 75%;
    margin: auto;
    text-align: left;
}

.field-blk{
    text-align: left;
    padding: 10px 0px 0px;
}

.field-title{
    font-size: 18px;
}

.field-input{
    width: 100%;    
    padding: 0px;
}

.field-input select{
    width: 100%;
    height: 50px;
    font-size: 18px;
    outline: none;
    padding-left: 15px;
    cursor: pointer;
    border: 1px solid #0000002e;
    border-radius: 4px;
    color: #151515d6;
}

.date-blk {
    padding: 0px;
}

.p-l-10{
    padding-left: 10px;
}

.p-r-10{
    padding-right: 10px;
}

.date-blk .react-date-picker{
    height: 50px;
    width: 100%;
    border: 1px solid #0000002e;
    border-radius: 4px;
    color: #151515d6;    
    font-size: 18px;
    outline: none;
    padding-left: 15px;
    text-align: left;
    float: left;
}

.react-date-picker__wrapper{
    border: none;
}

.button-blk{
    padding: 20px 0px 20px;
    text-align: center;
}

.button{
    height: 50px;
    width: 120px;
    outline: none;
    font-size: 18px;
    color: white;
    background-color: #E72744;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    transition: -webkit-transform .2s;
    transition: transform .2s;
    transition: transform .2s, -webkit-transform .2s;
}

.button:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.error-blk{
    text-align: left;
    font-style: italic;
    padding-top: 10px;
    color: red;
}

.asterik{
    color: red;
}

.passenger-error{
    padding-left: 20px;
}

.swal2-actions button{
    background-color: #E72744 !important;
}

@media only screen and (max-width: 400px) {
    
    .booking-form{
        width: 80%;
        padding: 2%;
        background: white;
        margin: 35px 10%;
    }
    .form-field{
        width: 80%;
    }
    .date-blk .react-date-picker{
        width: 100%;
    }

    .p-l-10{
        padding-left: 0px;
    }
    
    .p-r-10{
        padding-right: 0px;
    }
}
